@import "_variables.scss";
@import "_footer.scss";
@import "_globalYbody.scss";
@import "_header.scss";

main {
    #contenido {
        display: flex;
        //justify-content: space-between;
        //margin-left: -400px;
        margin-left: 18px;
        //margin-right: 300px;
        margin-top: 100px;
        margin-bottom: 100px;
        gap: 20px;
        

        #formulario {
            background-color: $rosaOscuro;
            //border: 1px solid black;
            width: 60%;
            border-radius: 10px;
            

            fieldset {
                border: 0px solid black;
                padding-left: 100px;
                padding-right: 100px;

                h2 {
                    text-align: center;
                    font-family: $fuenteTitulos;
                }
                input {
                    width: 100%;
                    height: 30px;
                    border-radius: 5px;
                    margin-top: -7px;
                }
                #segundoInput {
                    height: 100px;
                }
                button {
                    font-family: $fuenteTitulos;
                    color: black;
                    background-color: $verdeClaro;
                    display: block;
                    margin-left: auto;
                    margin-right: auto;
                    border-radius: 10px;
                    padding: 6px;
                    padding-left: 40px;
                    padding-right: 40px;
                    margin-top: 25px;
                    margin-bottom: 25px;
                }
                label {
                    font-family: $fuenteTexto;
                }
            }
        }
    }

    #textoContacto {
        //background-color: white;
        //border: 1px solid black;
        padding-top: 35px;
        font-size: 25px;
        font-family: $fuenteTexto;

        p {
            color: black;
        }
        li {
            color: black;
            margin-left: 50px;
        }
    }
}
